<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
			>
			<el-form-item class="add-item" label="" prop="ziyuanmingcheng">
				<el-input v-model="ruleForm.ziyuanmingcheng" 
					placeholder="" clearable :disabled=" false  ||ro.ziyuanmingcheng"></el-input>
			</el-form-item>
			<el-form-item class="add-item"  label="" prop="ziyuanleixing">
				<el-select v-model="ruleForm.ziyuanleixing" placeholder="" :disabled=" false  ||ro.ziyuanleixing" >
					<el-option
						v-for="(item,index) in ziyuanleixingOptions"
						:key="index"
						:label="item"
						:value="item">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.ziyuantupian)" prop="ziyuantupian">
				<file-upload
					tip=""
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.ziyuantupian?ruleForm.ziyuantupian:''"
					@change="ziyuantupianUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="ziyuantupian">
				<img v-if="ruleForm.ziyuantupian.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.ziyuantupian.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.ziyuantupian.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item" label="" prop="ziyuanwenjian">
				<file-upload
					tip=""
					action="file/upload"
					:limit="1"
					:type="3"
					:multiple="true"
					:fileUrls="ruleForm.ziyuanwenjian?ruleForm.ziyuanwenjian:''"
					@change="ziyuanwenjianUploadChange"
					></file-upload>
			</el-form-item>  
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.ziyuanshipin)" prop="ziyuanshipin">
				<file-upload
					tip=""
					action="file/upload"
					:limit="1"
					:type="2"
					:multiple="true"
					:fileUrls="ruleForm.ziyuanshipin?ruleForm.ziyuanshipin:''"
					@change="ziyuanshipinUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="ziyuanshipin">
				<div class="viewBtn" v-if="ruleForm.ziyuanshipin" @click="download(baseUrl + ruleForm.ziyuanshipin)" type="success"></div>
				<div class="unviewBtn" v-else disabled></div>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="fabushijian">
				<el-date-picker
					:disabled=" false  ||ro.fabushijian"
					value-format="yyyy-MM-dd HH:mm:ss"
					v-model="ruleForm.fabushijian" 
					type="datetime"
					placeholder="">
				</el-date-picker>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="laoshizhanghao">
				<el-input v-model="ruleForm.laoshizhanghao" 
					placeholder="" clearable :disabled=" false  ||ro.laoshizhanghao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="laoshixingming">
				<el-input v-model="ruleForm.laoshixingming" 
					placeholder="" clearable :disabled=" false  ||ro.laoshixingming"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="shiyongfanwei">
				<el-input
					type="textarea"
					:rows="8"
					placeholder=""
					v-model="ruleForm.shiyongfanwei">
					</el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="ziyuanjianjie">
				<editor 
					v-model="ruleForm.ziyuanjianjie" 
					class="editor" 
					action="file/upload">
				</editor>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont "></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont "></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					ziyuanmingcheng : false,
					ziyuanleixing : false,
					ziyuantupian : false,
					ziyuanwenjian : false,
					ziyuanshipin : false,
					shiyongfanwei : false,
					ziyuanjianjie : false,
					fabushijian : false,
					laoshizhanghao : false,
					laoshixingming : false,
					clicktime : false,
					clicknum : false,
					discussnum : false,
					storeupnum : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					ziyuanmingcheng: '',
					ziyuanleixing: '',
					ziyuantupian: '',
					ziyuanwenjian: '',
					ziyuanshipin: '',
					shiyongfanwei: '',
					ziyuanjianjie: '',
					fabushijian: '',
					laoshizhanghao: '',
					laoshixingming: '',
					clicktime: '',
					clicknum: '',
					discussnum: '',
					storeupnum: '',
				},
				ziyuanleixingOptions: [],


				rules: {
					ziyuanmingcheng: [
					],
					ziyuanleixing: [
					],
					ziyuantupian: [
					],
					ziyuanwenjian: [
					],
					ziyuanshipin: [
					],
					shiyongfanwei: [
					],
					ziyuanjianjie: [
					],
					fabushijian: [
					],
					laoshizhanghao: [
					],
					laoshixingming: [
					],
					clicktime: [
					],
					clicknum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					discussnum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					storeupnum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='ziyuanmingcheng'){
							this.ruleForm.ziyuanmingcheng = obj[o];
							this.ro.ziyuanmingcheng = true;
							continue;
						}
						if(o=='ziyuanleixing'){
							this.ruleForm.ziyuanleixing = obj[o];
							this.ro.ziyuanleixing = true;
							continue;
						}
						if(o=='ziyuantupian'){
							this.ruleForm.ziyuantupian = obj[o].split(",")[0];
							this.ro.ziyuantupian = true;
							continue;
						}
						if(o=='ziyuanwenjian'){
							this.ruleForm.ziyuanwenjian = obj[o];
							this.ro.ziyuanwenjian = true;
							continue;
						}
						if(o=='ziyuanshipin'){
							this.ruleForm.ziyuanshipin = obj[o];
							this.ro.ziyuanshipin = true;
							continue;
						}
						if(o=='shiyongfanwei'){
							this.ruleForm.shiyongfanwei = obj[o];
							this.ro.shiyongfanwei = true;
							continue;
						}
						if(o=='ziyuanjianjie'){
							this.ruleForm.ziyuanjianjie = obj[o];
							this.ro.ziyuanjianjie = true;
							continue;
						}
						if(o=='fabushijian'){
							this.ruleForm.fabushijian = obj[o];
							this.ro.fabushijian = true;
							continue;
						}
						if(o=='laoshizhanghao'){
							this.ruleForm.laoshizhanghao = obj[o];
							this.ro.laoshizhanghao = true;
							continue;
						}
						if(o=='laoshixingming'){
							this.ruleForm.laoshixingming = obj[o];
							this.ro.laoshixingming = true;
							continue;
						}
						if(o=='clicktime'){
							this.ruleForm.clicktime = obj[o];
							this.ro.clicktime = true;
							continue;
						}
						if(o=='clicknum'){
							this.ruleForm.clicknum = obj[o];
							this.ro.clicknum = true;
							continue;
						}
						if(o=='discussnum'){
							this.ruleForm.discussnum = obj[o];
							this.ro.discussnum = true;
							continue;
						}
						if(o=='storeupnum'){
							this.ruleForm.storeupnum = obj[o];
							this.ro.storeupnum = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
						if((json.laoshizhanghao!=''&&json.laoshizhanghao) || json.laoshizhanghao==0){
							this.ruleForm.laoshizhanghao = json.laoshizhanghao;
							this.ro.laoshizhanghao = true;
						}
						if((json.laoshixingming!=''&&json.laoshixingming) || json.laoshixingming==0){
							this.ruleForm.laoshixingming = json.laoshixingming;
							this.ro.laoshixingming = true;
						}
					}
				});
				this.$http.get('option/ziyuanleixing/ziyuanleixing', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ziyuanleixingOptions = res.data.data;
					}
				});

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 
			// 
			info() {
				this.$http.get(`xuexiziyuan/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`xuexiziyuan/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
			ziyuantupianUploadChange(fileUrls) {
				this.ruleForm.ziyuantupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
			ziyuanwenjianUploadChange(fileUrls) {
				this.ruleForm.ziyuanwenjian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
			ziyuanshipinUploadChange(fileUrls) {
				this.ruleForm.ziyuanshipin = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
