<script>
	import 'animate.css';

export default {
    //
    data() {
		return {
            pageFlag : '',
			tableName: '',
			registerForm: {},
			forgetForm: {},
			rules: {},
			requiredRules: {},
            xueshengxingbieOptions: [],
            laoshixingbieOptions: [],
		}
    },
	mounted() {
		if(this.$route.query.pageFlag=='register'){
			this.tableName = this.$route.query.role;
			if(this.tableName=='xuesheng'){
				this.registerForm = {
					xuehao: '',
					xingming: '',
					mima: '',
					mima2: '',
					shoujihao: '',
					xingbie: '',
					nianling: '',
					banji: '',
					zhuanye: '',
					touxiang: '',
				}
			}
			if(this.tableName=='laoshi'){
				this.registerForm = {
					laoshizhanghao: '',
					laoshixingming: '',
					mima: '',
					mima2: '',
					lianxifangshi: '',
					xingbie: '',
					nianling: '',
					jiaoshizhicheng: '',
					touxiang: '',
				}
			}
			if ('xuesheng' == this.tableName) {
				this.rules.xuehao = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.xuehao = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('xuesheng' == this.tableName) {
				this.rules.xingming = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.xingming = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('xuesheng' == this.tableName) {
				this.rules.mima = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.mima = [{ required: true, message: '', trigger: 'blur' }]
			}
			this.xueshengxingbieOptions = ",".split(',');
			if ('laoshi' == this.tableName) {
				this.rules.laoshizhanghao = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.laoshizhanghao = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('laoshi' == this.tableName) {
				this.rules.laoshixingming = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.laoshixingming = [{ required: true, message: '', trigger: 'blur' }]
			}
			if ('laoshi' == this.tableName) {
				this.rules.mima = [{ required: true, message: '', trigger: 'blur' }];
				this.requiredRules.mima = [{ required: true, message: '', trigger: 'blur' }]
			}
			this.laoshixingbieOptions = ",".split(',');
		}
	},
    created() {
		this.pageFlag = this.$route.query.pageFlag;
    },
    //
    methods: {
		changeRules(name){
			if(this.requiredRules[name]){
				return true
			}
			return false
		},
		// uuid
		getUUID () {
			return new Date().getTime();
		},
        // 
		xueshengtouxiangUploadChange(fileUrls) {
			this.registerForm.touxiang = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
		},
		laoshitouxiangUploadChange(fileUrls) {
			this.registerForm.touxiang = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
		},

		// 


		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					var url=this.tableName+"/register";
					if(`xuesheng` == this.tableName && this.registerForm.mima!=this.registerForm.mima2) {
						this.$message.error(``);
						return
					}
					if(`laoshi` == this.tableName && this.registerForm.mima!=this.registerForm.mima2) {
						this.$message.error(``);
						return
					}
					this.$http.post(url, this.registerForm).then(res => {
						if (res.data.code === 0) {
							this.$message({
								message: '',
								type: 'success',
								duration: 1500,
								onClose: () => {
									this.$router.push('/login');
								}
							});
						} else {
							this.$message.error(res.data.msg);
						}
					});
				} else {
					return false;
				}
			});
		},
		resetForm(formName) {
			this.$refs[formName].resetFields();
		}
    }
}
</script>

